import React from "react";

function Project(props) {
    return (
        <div>

            <div className="col-md-6 col-lg-4 mb-5" style={{ width: "1000px" }}>
                {/* div below controls rounding of square corners and modal on and off. */}
                <div className="portfolio-item mx-auto" data-toggle="modal" data-target={props.modal}>
                    <div
                        className="portfolio-item-caption d-flex align-items-center justify-content-center h-100 w-100">
                        <div className="portfolio-item-caption-content text-center text-white"><i
                            className="fas fa-plus fa-3x"></i></div>
                    </div><img className="img-fluid" src={props.img}
                        alt={props.altText} />
                </div>
            </div>


        </div>
    )
}

export default Project;